document.addEventListener('input', function (event)
{
	if (event.target.id !== 'colors') return;
	let color = event.target.value;
	let p2NameColor = document.getElementById("player2Name").style.color;
	let p3NameColor = document.getElementById("player3Name").style.color;
	if(color == p2NameColor || color == p3NameColor){ // this check does not work
		console.alert("Sorry, this color is taken by another player");
		return;
	}
	let playerName = document.getElementById("player1Name");
	let tile1 = document.getElementById("player1Tile1");
	let tile2 = document.getElementById("player1Tile2");

	if(color == "green")
	{
		let setColor = rgb(34, 136, 51);
	}
	else if(color == "blue")
	{
		let setColor = rgb(51, 187, 238);
	}
	else if(color == "red")
	{
		let setColor = rgb(204, 51, 17);
	}
	else if(color == "purple")
	{
		let setColor = rgb(238, 51, 119);
	}
	playerName.style.color = setColor; //setColor var does not work
	tile1.style.borderColor = setColor;
	tile2.style.borderColor = setColor;
}, false);


